<template>

    <!-- login-area -->
    <section class="login-area">
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-6 col-lg-7">
                    <div class="login-left row">
                        <div class="introduction">
                            <div class="heading wow fadeInLeft" data-wow-duration="1s" data-wow-delay=".5s">
                                <h1>Welcome to CRM</h1>
                                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Reiciendis architecto laborum tenetur natus</p>
                            </div>
                            <div class="heading-img wow fadeInLeft" data-wow-duration="1s" data-wow-delay="1s">
                                <img class="img-fluid" src="../assets/img/Frame.svg" alt="image">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-lg-5">
                    <div class="login-right row">
                        <div class="login-form custom-form">
                            <!-- logo -->
                            <div class="logo-login wow fadeInUp" data-wow-duration="1s" data-wow-delay="1s">
                                <a href="index.html"><img class="img-fulid" src="../assets/img/logo.svg" alt="logo"></a>
                            </div>
                            <!-- logo -->
                            <div class="login-title wow fadeInLeft" data-wow-duration="1s" data-wow-delay="1s">
                                <h2>Login to your account</h2>
                            </div>
                            <form class="wow fadeInRight login_" data-wow-duration="1s" data-wow-delay="1s" v-on:submit.prevent = "loginProcess" >
                                <div class="login-error-message text-center" v-if="authError"> Invalid Email or Password</div>
                                <div class="form-group label-design" v-on:click="inputLabelStyle($event)">
                                    <label class="label-class" for="inlineFormInputGroup">Email address</label>  
                                    <div class="input-group">
                                        <div class="input-group-prepend">
                                          <div class="input-group-text"><img class="img-fluid" src="../assets/img/Account.svg" alt=""></div>
                                        </div>
                                        <input type="email" class="form-control" @focus="inputLabelStyle($event,'parent')" id="inlineFormInputGroup" placeholder="" v-model="login_.email">
                                    </div>
                                </div>
                                <div class="validate-error-message" v-if="$vd.login_.email.$hasError === true">{{$vd.login_.$errors.email[0]}}</div>
                                <div class="form-group label-design"  v-on:click="inputLabelStyle($event)">
                                    <label class="label-class" for="exampleInputPassword1">Password</label>
                                    <div class="input-group">
                                        <div class="input-group-prepend">
                                          <div class="input-group-text"><img class="img-fluid" src="../assets/img/lock.svg" alt=""></div>
                                        </div>
                                        <input v-bind:type="pType" @focus="inputLabelStyle($event,'parent')" class="form-control" id="exampleInputPassword1" placeholder="" v-model="login_.password">
                                        <div class="input-group-text show-passs"><img class="img-fluid show-eye" src="../assets/img/Eye.svg" alt="" v-on:click="showPass()"> <img class="img-fluid hide-eye" src="../assets/img/eyeclose.svg" alt="" v-on:click="hidePass()"></div>
                                    </div>
                                </div>
                                <div class="validate-error-message" v-if="$vd.login_.password.$hasError === true">{{$vd.login_.$errors.password[0]}}</div>
                                <div class="form-group form-check">
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" class="custom-control-input" id="customCheckBox1">
                                        <label class="custom-control-label" for="customCheckBox1">Remember me</label>
                                    <a class="forgot" href="#">Forgot password</a>
                                    </div>
                                </div>

                                <button type="submit" class="login-btn" >Login</button>
                                <!-- <p class="allready">You don’t have a crm? <a href="#">Signup</a></p> -->
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <!-- login-area -->

</template>

<script>
import $ from 'jquery'
import VueDaval from 'vue-daval';
import axios from 'axios'

export default {
    name: 'Login',
    beforeCreate: function () {
        var auth = this.$storage.get('auth')
        if (auth) {
            this.$router.push('/dashboard')
        }
    },
    mixins: [ VueDaval ],
	data(){
		return{
            login_: {email:'',password:''},
            pType:  'password',
            authError: false,
		}
    },
    vdRules: {
        login_: {
            email: { required: true, type: 'email' },
            password: {required: true},
        }   
    },
	methods:{
		loginProcess(){
            this.$vd.login_.$validate().then(() => {
                axios
                .post('login',this.login_)
                .then( res => {
                    this.authError = false;
                    if (res.data.success) {
                        this.$storage.set('auth', res.data.success)
                        axios.defaults.headers.common['Authorization'] = res.data.success.token;
                        this.$router.push('/dashboard');
                    }
                })
                .catch(err =>{

                    this.authError = true;
                    //setTimeout(() => this.authError=false, 5000);
                    console.log(err)
                });

            }).catch(() => {

            });
        },
        showPass(){
            this.pType = 'text';
            $(".show-passs .hide-eye").show();
            $(".show-passs .show-eye").hide();
        },
        hidePass(){
            this.pType = 'password';
            $(".show-passs .hide-eye").hide();
            $(".show-passs .show-eye").show();
        },
        inputLabelStyle(event,type=null){
            if(type == 'parent'){
                $(event.currentTarget).parent().parent().addClass('input-border');
                $(event.currentTarget.parentElement.parentElement.querySelector('label')).addClass('label-top');
            }else{
                $(event.currentTarget).addClass('input-border');
                $(event.currentTarget.querySelector('label')).addClass('label-top');
            }
        }
        
    },
    mounted(){
        this.hidePass();
    }
}
</script>